.page__body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 30px;
}

.page__ally-hidden {
  position: absolute;
  transform: scale(0);
}

.page {
  height: 100%;
}

.page__leveler {
  margin: 0 auto;
  width: 260px;

  @media (min-width: 768px) {
    width: 668px;
  }

  @media (min-width: 1150px) {
    width: 1050px;
  }

  &--mobile-on {
    @media (min-width: 768px) {
      display: contents;
    }
  }

  &--mobile-off {
    @media (max-width: 768px - 0.05px) {
      display: contents;
    }
  }

  &--tablet-on {
    @media (min-width: 750px) and (max-width: 1149px) {
      display: contents;
    }
  }

  &--desktop-on {
    @media (min-width: 1150px) {
      display: contents;
    }
  }
}

.page__vertical-leveler {
  padding-top: 30px;
  padding-bottom: 60px;

  @media (min-width: 768px) {
    padding-top: 67px;
    padding-bottom: 64px;
  }

  @media (min-width: 1150px) {
    padding-top: 132px;
    padding-bottom: 103px;
  }
}

.page__break {
  @media (min-width: 768px) and (max-width: 1149px) {
    display: none;
  }
}

.page__break--form {
  @media (min-width: 768px) {
    display: none;
  }
}

.page__modal-fixed {
  position: fixed;
  top: 0;
  left: 0;
  // transform: translate(-50%, -50%);
  z-index: 100;
}
