.checkbox-filter {
  padding-top: 35px;

  @media (min-width: 768px) {
    background-color: var(--lightgrey);
    padding-top: 67px;
    padding-bottom: 12px;
  }

  @media (min-width: 1150px) {
    padding-bottom: 11px;
  }
}

.checkbox-filter__list {
  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    width: 500px;
    column-gap: 31px;
  }

  @media (min-width: 1150px) {
    width: 760px;
    column-gap: 33px;
  }
}

.checkbox-filter__item {
  margin-bottom: 43px;

  @media (min-width: 768px) {
    width: 230px;
    margin-bottom: 57px;
  }
}

.checkbox-filter__label {
  position: relative;
  display: block;
  cursor: pointer;
  user-select: none;
  padding-left: 61px;
}

.checkbox-filter__input:checked + .checkbox-filter__label::before {
  content: "";
  position: absolute;
  left: 7px;
  top: 1px;
  width: 25px;
  height: 17px;
  background-image: url("../../icons/sprite.svg#checkbox-center");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (min-width: 768px) {
    left: -11px;
    top: -1px;
    width: 32px;
    height: 22px;
  }

  @media (min-width: 1150px) {
    top: -3px;
  }
}

.checkbox-filter__label::after {
  content: "";
  position: absolute;
  left: 0;
  top: -10px;
  width: 38px;
  height: 38px;
  border: 2px solid #000000;

  @media (min-width: 768px) {
    background-color: transparent;
    width: 52px;
    height: 52px;
    left: -21px;
    top: -17px;
  }

  @media (min-width: 1150px) {
    top: -18px;
  }
}

.checkbox-filter__label:hover::after {
  border-color: var(--lightgreen-form);
}

.checkbox-filter__label:active::after {
  border-color: hsla(0, 0%, 27%, 0.3);
}

.checkbox-filter__title {
  @media (min-width: 768px) {
    margin-left: 20px;
    margin-right: 144px;
  }

  @media (min-width: 1150px) {
    margin-left: 0;
    margin-right: 91px;
  }
}
