.additionally {
  padding-top: 23px;

  @media (min-width: 768px) {
    padding-top: 50px;
    padding-bottom: 49px;
  }

  @media (min-width: 1150px) {
    padding-top: 50px;
    padding-bottom: 48px;
  }
}

.additionally__area {
  font-family: inherit;
  resize: none;
  border: 2px solid var(--charcoal);
  width: 260px;
  height: 142px;
  font-size: 17px;
  line-height: 18px;
  padding: 16px 15px;

  @media (min-width: 768px) {
    font-size: 19px;
    width: 501px;
  }

  @media (min-width: 1150px) {
    width: 700px;
  }
}

.additionally__area:hover {
  border-color: var(--lightgreen);
}

.additionally__area:focus {
  outline-color: var(--lightgreen);
  border-radius: 0;
}

.additionally__title {
  @media (min-width: 768px) {
    padding-top: 20px;
    margin-right: 128px;
  }

  @media (min-width: 1150px) {
    margin-right: 75px;
  }
}
