.catalog {
  margin-bottom: 30px;

  @media (min-width: 768px) {
    margin-bottom: 52px;
  }

  @media (min-width: 1150px) {
    margin-bottom: 58px;
  }
}

.catalog__wrapper {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--lightgrey);
  border-top: none;

  @media (min-width: 768px) {
    min-height: 334px;
    display: grid;
    grid-template-columns: 333px 333px;
    grid-template-rows: min-content 1fr;
    border-top: 1px solid var(--lightgrey);
  }

  @media (min-width: 1150px) {
    min-height: 659px;
    display: flex;
  }
}

.catalog__wrapper:hover {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}

.catalog__wrapper:active {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

.catalog__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 31px;

  @media (min-width: 768px) {
    row-gap: 52px;
  }

  @media (min-width: 1150px) {
    flex-direction: row;
    gap: 48px;
  }
}

.catalog__product-photo {
  @media (min-width: 768px) {
    display: block;
    grid-row: 3 span;
    height: auto;
    width: auto;
  }
}

.catalog__photo-position {
  order: -10;
  grid-row: 3 span;
}

.catalog__size {
  font-weight: 400;
  font-size: 13px;
  margin: 0;
  margin-bottom: 18px;

  @media (min-width: 768px) {
    grid-column: 2/3;
    font-size: 15px;
  }

  @media (min-width: 1150px) {
    margin: 0;
  }
}

.catalog__price {
  position: relative;
  font-size: 15px;
  line-height: 24px;
  margin: 0;
  padding-top: 22px;
  padding-bottom: 26px;
  display: flex;
  justify-content: space-between;

  @media (min-width: 768px) {
    grid-column: 2/3;
    font-size: 17px;
    padding-bottom: 25px;
    padding-top: 25px;
  }

  @media (min-width: 1150px) {
    margin-top: auto;
    padding-bottom: 22px;
  }
}

.catalog__sum::before {
  content: "";
  position: absolute;
  background-color: var(--brightgray);
  height: 1px;
  width: 204px;
  top: -23px;
  left: 0;

  @media (min-width: 768px) {
    width: 268px;
    top: -25px;
  }

  @media (min-width: 1150px) {
    width: 256px;
  }
}

.catalog__title {
  margin: 0;
  font-size: 17px;
  line-height: 24px;
  padding-top: 18px;
  margin-bottom: 6px;

  @media (min-width: 768px) {
    grid-column: 2/3;
    font-size: 20px;
    line-height: 24px;
    padding-top: 28px;
    margin: 0;
    margin-bottom: 6px;
  }

  @media (min-width: 1150px) {
    padding-top: 28px;
    margin-bottom: 5px;
  }
}

.catalog__price,
.catalog__size,
.catalog__title {
  padding-left: 28px;
  padding-right: 27px;

  @media (min-width: 768px) {
    padding-left: 33px;
    padding-right: 31px;
  }

  @media (min-width: 1150px) {
    padding-left: 27px;
    padding-right: 32px;
  }
}

.catalog__sum {
  position: relative;
  margin: 0;
}

.catalog__cart {
  position: relative;
  width: 20px;
  height: 19px;
}

.catalog__cart::before {
  content: "";
  position: absolute;
  mask-image: url("../../icons/sprite.svg#cart");
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: cover;
  background-color: var(--black);
  opacity: 0.2;
  width: 21px;
  height: 18px;
  right: 0;
  cursor: pointer;

  @media (min-width: 768px) {
    right: 0;
  }

  @media (min-width: 1150px) {
    right: 0;
  }
}

.catalog__cart:hover::before {
  background-color: var(--lightgreen);
  opacity: 1;
}

.catalog__cart:active::before {
  background-color: var(--lightgreen);
  opacity: 0.3;
}

.catalog__link {
  cursor: pointer;
}

.catalog__link:hover {
  opacity: 0.6;
}

.catalog__link:active {
  opacity: 0.3;
}
